import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { CambioClimaticoPage } from './cambio-climatico.page';

const routes: Routes = [
  {
    path: '',
    component: CambioClimaticoPage
  },
  {
    path: 'ws-camb',
    loadChildren: () => import('../../Pages/cambio-climatico/ws-camb/ws-camb.module').then( m => m.WsCambPageModule)
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class CambioClimaticoPageRoutingModule {}
